@import url('https://fonts.googleapis.com/css2?family=Comic+Neue&display=swap');

body {
  min-height: 97vh;
  margin: 0;
  padding: 0;
  text-align: center;
  background:linear-gradient(25deg, #4B4E53, #000000);
  background-repeat: no-repeat;
  background-position: center;
  color: #ebeded;
  font-size: 1.1em;
  font-weight: 900;
  font-family: 'Comic Neue', 'SF Pro', 'Comic Sans MS', monospace;
}
ul {
  list-style-type: none;
}
li {
  display: inline;
  margin-right: 1.8vw;
}
#content {
  padding: 2.5vh 10vw 0vh;
}
#batman-symbol {
  text-align: center;
  width: 45%;
}
h1 {
  line-height: 1.1em;
  text-align: center;
}
h2 {
  font-size: 0.9em;
}
p {
  line-height: 1.5em;
}
a {
  color: #ebeded;
}
#back {
  text-align: center;
}
.App {
  text-align: center;
}

@media screen and (min-width: 800px) {
  body {
    min-height: 97vh;
    margin: 0;
    padding: 0;
    text-align: center;
    background:linear-gradient(25deg, #4B4E53, #000000);
    background-repeat: no-repeat;
    background-position: center;
    color: #ebeded;
    font-weight: 900;
    font-size: 120%;
    font-family: 'Comic Neue', 'SF Pro', 'Comic Sans MS', monospace;
  }
  ul {
    list-style-type: none;
  }
  li {
    display: inline;
    margin-right: 1.8vw;
  }
  #content {
    padding: 1.5vh 8.5vw 0vh;
  }
  #batman-symbol {
    text-align: center;
    width: 25%;
  }
  h1 {
    line-height: 1.1em;
    text-align: center;
  }
  h2 {
    font-size: 0.9em;
  }
  p {
    line-height: 1.5em;
  }
  a {
    color: #ebeded;
  }
  #back {
    text-align: center;
  }
}